import { Component } from '@angular/core';
import { NavController, NavParams, IonicPage } from 'ionic-angular';
import { Values } from '../../../providers/service/values';
import { Functions } from '../../../providers/service/functions';
import { CheckoutService } from '../../../providers/service/checkout-service';
//import { ShippingAddress } from '../shipping-address/shipping-address';
//import { ShippingPaymentMethod } from '../shipping-payment-method/shipping-payment-method';


/*
  Generated class for the AccountLoginPage page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@IonicPage()
@Component({
  templateUrl: 'billing-address-form.html',
})
export class BillingAddressForm {

  form: any;
  zone:any;
  status: any;
  billing: any;
  buttonText: any;
  errorMessage: any;
  public disableSubmit: boolean = false;
 
  
  constructor(public nav: NavController, public service: CheckoutService, params: NavParams, public functions: Functions, public values: Values) {

    this.billing = {};
    this.buttonText = this.values.lan.text_button_continue;  
    this.status  = [];

    this.service.getBillingAddressForm()
    .then((results) => this.handleBilingAddressForm(results));   
  }

  handleBilingAddressForm(results){
    this.form = results;
    this.getZone(results.country_id);
  }
 

  getZone(id) {
    this.form.zone_id = null;
    
    this.service.getZone(id)
    .then((results) => this.zone = results);
  }


  saveBilling(form) { 
    if(this.validateForm()){  
      this.disableSubmit= true;
      this.buttonText= this.form.text_loading; 
    
      this.service.saveBillingAddress(form)
      .then((results) => this.handleResults(results));
    }
  }

 

  handleResults(results){
    if(results.error){
      this.disableSubmit = false;
      this.buttonText= this.form.button_continue;
      this.status = results;
    }

    else if(this.form.shipping){
    this.service.saveShippingAddress(this.form)
    .then((results) => this.handleAddress(results));
    }

    else{
    this.disableSubmit = false;
    this.buttonText= this.form.button_continue;
    this.nav.push('ShippingAddress');
    }
  }

  
  handleAddress(results){
    this.disableSubmit = false;
    this.buttonText= this.form.button_continue;
    this.nav.push('ShippingPaymentMethod');
  }



  validateForm(){

    if(this.form.firstname == undefined || this.form.firstname == ""){return false}
    if(this.form.lastname == undefined || this.form.lastname == ""){return false}
    if(this.form.address_1 == undefined || this.form.address_1 == ""){return false}
    if(this.form.city== undefined || this.form.city == ""){return false}
    if(this.form.postcode == undefined || this.form.postcode == ""){return false}
    if(this.form.country_id == undefined || this.form.country_id == ""){return false}
    
    if(this.form.zone_id == undefined || this.form.zone_id == null){
        this.form.zone_id = "0";
    }
    return true
  }
   
}
  

  





